@use "global" as *;




.p-form-input {
  width: 100%;
  border: 1px solid $form-color;
  padding: 0.4rem 1.6rem;
  background-color: transparent;
  border-radius: 0;
  color: inherit;
  font-family: inherit;
  font-size: 1.4rem;
  height: 3.8rem;
  @include mq("md") {
    padding-top: 0.4rem;
    padding-bottom: 0.4rem;
  }
}
.p-form-checkbox + span {
  cursor: pointer;
  display: inline-block;
  margin: 0 0.2em 0;
  padding: 0 0 0 3rem;
  position: relative;
  font-size: 1.2rem;
  @include mq("md") {
    font-size: 1.6rem;
  }
}
.p-form-checkbox + span::before {
  background: $white;
  border: 1px solid rgba(0, 0, 0, 0.16);
  border-radius: 0.2rem;
  content: "";
  display: block;
  height: 2rem;
  height: 2rem;
  left: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 2rem;
  width: 2rem;
  @include mq("md") {
  }
}
.p-form-checkbox + span::after {
  border-bottom: 0.3rem solid $black;
  border-left: 0.3rem solid $black;
  content: "";
  display: block;
  height: 1rem;
  left: 0;
  margin-top: -0.2rem;
  opacity: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(-45deg);
  transition: all 0.3s ease 0s;
  width: 2rem;
  @include mq("md") {
  }
}
.p-form-checkbox:checked + span::after {
  opacity: 1;
}
.p-contact__message {
  text-align: center;
  margin-top: 6rem;
  display: none;

  &.-error {
    color: red;
  }
}
.p-contact-textarea {
  margin-top: 4.6rem;
  @include mq("md") {
    margin-top: 3.7rem;
  }
  dd {
    margin-top: 1.3rem;
    @include mq("md") {
      margin-top: 1.6rem;
    }
  }
}

.p-contact__check {
  margin-top: 2rem;
  text-align: center;

  @include mq("md") {
    margin-top: 1rem;
  }

  a {
    color: $contact-color;
    transition: all 0.3s ease 0s;
    font-weight: $bold;
  }
}
.p-form-input {
  width: 100%;
  border: 1px solid;
  background-color: $white;
  font-size: 1.8rem;
  padding: 1.5rem 1.6rem;
}
.p-form-textarea {
  width: 100%;
  padding: 1.7rem 0.8rem;
  height: 21.7rem;
  border: 1px solid $black;
  background-color: $white;
  font-size: 1.8rem;
  @include mq("md") {
    padding: 2.2rem 0.8rem;
    height: 40rem;
  }
}

/*リンクの形状*/
#page-top a {
  display: flex;
  justify-content: center;
  align-items: center;
  background: $skyblue;
  border-radius: 0.5rem;
  width: 6rem;
  height: 6rem;
  color: $black;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 0.6rem;
  transition: all 0.3s;
}

#page-top a:hover {
  background: $page-top-color;
}

/*リンクを右下に固定*/
#page-top {
  position: fixed;
  right: 1rem;
  bottom: 1rem;
  z-index: 211111;
  z-index: $layer-up;
  /*はじめは非表示*/
  opacity: 0;
  transform: translateX(100px);
}

/*左の動き*/

#page-top.LeftMove {
  animation: LeftAnime 0.5s forwards;
}

@keyframes LeftAnime {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/*右の動き*/

#page-top.RightMove {
  animation: RightAnime 0.5s forwards;
}
@keyframes RightAnime {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 1;
    transform: translateX(100px);
  }
}

//-----------------------skills-----------------------------

.p-skills {
}
// .p-skills__inner {
