.p-skills__items {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 3rem;
  @include mq("md") {
    flex-direction: row;
  }
}
.p-skills__item {
  padding: 5rem 5rem;
  background: white;
  border-radius: 2rem;
  width: 100%;
  margin-top: 2rem;
  @include mq("md") {
    width: calc(100% / 3);
    &:nth-child(n + 2) {
      margin-left: 2rem;
    }
  }
}

.p-skills__icon {
  text-align: center;
  margin-top: 1rem;
}

.p-skills__title {
  font-size: 1.8rem;
  text-align: center;
  margin-top: 1rem;
}
.p-skills__text {
  font-size: 1.2rem;
  text-align: center;
  margin-top: 1rem;
}
.p-top {
  display: flex;
  align-items: center;
}

.p-top__menu {
  transition: all 0.3s ease 0s;

  &:not(:last-child) {
    margin-right: rem(40);
  }

  a {
    position: relative;
    display: inline-block;
    text-decoration: none;
  }
  a::after {
    position: absolute;
    bottom: -4px;
    left: 0;
    content: "";
    width: 100%;
    height: 1px;
    background: #333;
    transform: scale(0, 1);
    transform-origin: left top;
    transition: transform 0.3s;
  }
  a:hover::after {
    transform: scale(1, 1);
  }
}
.p-works__lists {
  display: flex;
  justify-content: center;
  padding: 2rem;
  background-color: $white;
  border-radius: 2rem;
  flex-direction: column;
  margin: 5rem 1.5rem 0;
  height: 80rem;

  @include mq("md") {
    align-items: center;
    flex-direction: row;
    height: 80rem;
  }
}

.p-works__list {
  transition: all 0.3s ease 0s;
  width: rem(350);

  &:hover {
    opacity: 0.7;
    transition: all 0.3s ease 0s;
  }
}

.p-works__items {
  display: flex;
}

.p-works__item {
  width: 100%;
}

.p-works__top {
  font-size: 2rem;
}

.p-works__list--right {
  font-size: 1.6rem;
  @include mq("md") {
    margin: 0 auto;
  }
}

.p-works__list--left {
  @include mq("md") {
    text-align: center;
    margin: 0 auto;
  }
}

.p-works__img {
  margin-top: 1rem;
  @include mq("md") {
    max-width: 43rem;
    width: 100%;
  }
}

.p-works__list__items {
  border-bottom: solid 2px black;
  padding: 1rem 0;
}

.p-works__list__item {
  padding-top: 2rem;
}

